<template>
  <div class="invitation full-screen" v-show="isInit">
    <div class="invitation-bg"></div>
    <div class="invitation-logo"></div>
    <div class="invitation-avatar">
      <img :src="avatar" @error="defaultImage"/>
    </div>
    <div class="invitation-info">
      <h1>{{ name }}</h1>
      <h2>邀请您成为清科项目工场优质投资人。</h2>
      <h3>快成为项目工场认证投资人，一起发现好项目吧！</h3>
    </div>
    <div class="invitation-btn" @click="gotoLogin"></div>
    <div class="invitation-text"></div>
  </div>
</template>

<script type="text/ecmascript-6">
import storage from 'common/js/storage.js'
import { setTitle, defaultInvestorImg, prohibitSliding } from 'common/js/util'
import { MiniSiteApi, ImgPath } from 'api'

import tempAvatarUrl from 'common/image/icon-default-avatar@2x.png'

export default {
  data () {
    return {
      avatar: '',
      name: '',
      isInit: false,
      logoUrl: null
    }
  },
  activated () {
    // http://localhost:8080/pemweb/invitation?inviterzj=FD3C6816D20847069009806D88E02905
    this._setTitle()
    this.logoUrl = defaultInvestorImg(this.inviterzj) || tempAvatarUrl
    this.inviterzj = this.$route.query.inviterzj
    this._getInvitedInfo(this.$route.query.inviterzj)
    prohibitSliding()
  },
  deactivated () {
    setTitle('项目工场')
  },
  methods: {
    _setTitle () {
      setTitle('项目工场邀请函')
    },
    _getInvitedInfo (inviterzj) {
      MiniSiteApi.getInvitedInfo(inviterzj).then(data => {
        if (+(data.resultcode) === 1) {
          this.name = data.name
          this.avatar = data.logo ? (ImgPath + data.logo) : this.logoUrl
          storage.set('__pemweb__minisite__inviter__', data.inviterzj)
          this.isInit = true
          this.wxShare({
            title: `${data.name}邀请您成为清科项目工场优质投资人`,
            content: '找好项目，就在项目工场',
            imgUrl: this.avatar || this.logoUrl,
            linkUrl: window.location.href
          })
        }
      })
    },
    gotoLogin () {
      MiniSiteApi.setAcceptInvitationCount().then(data => {
        this.$router.push('/login')
      })
    },
    defaultImage (e) {
      e.target.src = this.logoUrl
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  $color-black = #141414
  .full-screen
    background: $color-black
  .invitation
    font-size: 0.24rem
    background-color: $color-black
    width:7.5rem
    position: relative
    bg-image('image-bg')
    background-size: cover
    background-position: center
    .invitation-bg
      width: 7.5rem
      height: 100%
    .invitation-logo
      width: 2.32rem
      height: 0.6rem
      bg-image('icon-logo')
      background-size: 100%
      position: absolute
      top: 0.48rem
      left: 0.48rem
    .invitation-info
      position: absolute
      left: 0.46rem
      bottom: 5.84rem
      width: 6.5rem
      h1
        font-size: 0.56rem
        color: #FFDA33
        letter-spacing: -0.002rem
        line-height: 0.8rem
      h2
        font-size: 0.36rem
        color: #FFFFFF
        letter-spacing: -0.0016rem
        line-height: 0.5rem
      h3
        font-size: 0.28rem
        color: #7C7C7C
        letter-spacing: -0.0016rem
        line-height: 0.4rem
    .invitation-btn
      position: absolute
      left: 0.5rem
      bottom: 4.04rem
      width: 6.5rem
      height: 0.9rem
      bg-image('icon-button')
      background-size: 100%
    .invitation-text
      position: absolute
      bg-image('text-01')
      background-size: 100%
      background-repeat: no-repeat
      width: 3.06rem
      height: 2.28rem
      bottom: 0.86rem
      left: 3.84rem
    .invitation-avatar
      position: absolute
      width: 1.18rem
      height: 1.18rem
      border: 1px solid #fff
      border-radius: 1.18rem
      bottom: 7.84rem
      left: 0.5rem
      background-size: 100%
      background-color: #fff
      img
        width: 100%
        height: 100%
        border-radius: 50%
</style>



